.destination-entry-item {
  display: -ms-flexbox;
  display: flex;
  border-bottom: 2px solid lightgray;
}

.destination-entry-info {
  padding: 0.8em;
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.estimates-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  width: 40vw;
}

.destination-long-name {
  /* Vertically centers text in item - must be same as list item height */
  line-height: 10vw;
  font-size: 5vw;
}

.line-color {
  display: inline-block;
  width: 2vw;
}

.line-color-red {
  background-color: rgb(239, 14, 14);
}

.line-color-green {
  background-color: rgb(14, 239, 66);
}

.line-color-blue {
  background-color: rgb(14, 62, 239);
}

.line-color-orange {
  background-color: rgb(239, 145, 14);
}

.line-color-yellow {
  background-color: rgb(239, 235, 9);
}



